import React from 'react'
import propTypes, { element } from 'prop-types'

import Constants from '../../../Blended/Constants'

import Styles from './Pagination.css'

const iconBack =
  'data:image/png;base64,'
const iconForward =
  'data:image/png;base64,'

class Pagination extends React.Component {
  render() {
    const {
      rootClassName,
      contentClassName,
      icons,
      element,
      limit,
      offset,
      total,
      onPageChange
    } = this.props

    const disableBack = offset - limit > 0 ? false : true
    const disableForward = offset + limit < total ? false : true

    return (
      <div className={`${Styles['rbc-pagination']} ${rootClassName || ''}`}>
        <div
          className={`${Styles['rbc-pagination-content']} ${
            contentClassName || ''
          }`}
        >
          <div
            className={`${Styles['rbc-pagination-icon-back']} ${
              disableBack ? Styles['rbc-pagination-icon-back-disabled'] : ''
            }`}
          >
            {icons && icons.back ? (
              icons.back
            ) : (
              <img
                className={Styles['rbc-pagination-icon-back-icon']}
                src={iconBack}
                onClick={() => onPageChange('back')}
              />
            )}
          </div>
          {element ? (
            element
          ) : (
            <span className={Styles['rbc-pagination-icon-element']}>{`${
              (offset || 0) + 1
            } - ${(offset || 0) + (limit || 0) || 0} of ${total}`}</span>
          )}
          <div
            className={`${Styles['rbc-pagination-icon-forward']} ${
              disableForward
                ? Styles['rbc-pagination-icon-forward-disabled']
                : ''
            }`}
          >
            {icons && icons.forward ? (
              icons.forward
            ) : (
              <img
                className={Styles['rbc-pagination-icon-forward-icon']}
                src={iconForward}
                onClick={() => onPageChange('forward')}
              />
            )}
          </div>
        </div>
      </div>
    )
  }
}

Pagination.propTypes = {
  rootClassName: propTypes.string,
  contentClassName: propTypes.string,
  icons: propTypes.object,
  element: propTypes.element,
  limit: propTypes.number.isRequired,
  offset: propTypes.number.isRequired,
  total: propTypes.number.isRequired,
  onPageChange: propTypes.func.isRequired
}

Pagination.defaultProps = {}

export default Pagination
